import './styles/app.css';

// import BookService from  './services/BookService';
import UI from './UI';

document.addEventListener('DOMContentLoaded', ()=>{
  const ui = new UI();
  ui.renderBooks();
});

document.getElementById('libro-form')
  .addEventListener('submit', e => {
    const titulo = document.getElementById('titulo').value;
    const autor = document.getElementById('autor').value;
    const id = document.getElementById('id').value;
    const image = document.getElementById('image').files;

    const formData = new FormData();
    formData.append('image', image[0]);
    formData.append('titulo', titulo);
    formData.append('autor', autor);
    formData.append('id', id);

    //console.log(title, autor, id, image);
    // const bookService = new BookService()
    // bookService.postBook(formData);

    const ui = new UI();
    ui.addANewBook(formData);
    // ui.renderMessage('Nuevo libro agregado', 'success', 2000);

    e.preventDefault();
});

document.getElementById('libros-cards')
  .addEventListener('click', e =>{
    //console.log('click');

    if(e.target.classList.contains('delete')){
      //console.log('Eliminando');
      //console.log(e.target.getAttribute('_id')); 
      const ui = new UI();

      ui.deleteBook(e.target.getAttribute('_id'));
     // ui.renderMessage('Nuevo eliminado', 'danger', 2000);

    }
    e.preventDefault();

  });